        },
      ],
      pagenum: [
        { num: "1" },
        { num: "2" },
        { num: "3" },
        { num: "4" },
        { num: "5" },
        { num: "6" },
        { num: "7" },
      ],

      province: [], //省
      citylist: [], // 市
      arealist: [], // 区
