      city: "济南市", // 市
      area: "历下区",
      DistrictList: [], // 区数据
      brandtitle: "品牌",
      show: false,
      shows: false,
      shows2: false,
      shows3: false,
      brand: [
        { show: false, list: "品牌一" },
