      ],
      indexflag: "",
      indexp: 0,
      indexshi: 0,
      indexpages: 0,
      page: 1,
      current: 0,
    };
  },
  methods: {
    brandclick() {
      this.show = !this.show;
    },
    brandchoose(e) {
      this.brandtitle = this.brand[e].list;
      this.show = false;
    },
    provincelist() {
      this.shows = !this.shows;
    },
    provincechoose(e) {
      this.indexp = e;
      this.province = this.list[e].province;
      this.citylist = this.list[e].city;
      // console.log(this.list[e].city);
      // this.list[this.index].show = false;
    },
    citylists() {
      this.shows2 = !this.shows2;
    },
    citychoose(e) {
      this.city = this.list[this.indexp].city[e].city;
      this.indexshi = e;
      this.arealist = this.citylist[e].area;
      // console.log(this.citylist[e].area);
    },
    arealists() {
      this.shows3 = !this.shows3;
    },
    areachoose(e) {
      this.area = this.arealist[e].area;
    },
    pagechange(e) {
      this.indexpages = e;
      this.page = e + 1;
      // console.log(this.indexpages);
    },
    prev() {
      if (this.current <= 0) {
        this.current = 0;
      } else {
        this.current--;
      }
      this.page = this.current + 1;
      this.indexpages = this.current;
    },
    next() {
      if (this.current >= 2) {
        this.current = this.current;
      } else {
        this.current++;
      }
      this.page = this.current + 1;
      this.indexpages = this.current;
    },
  },
  computed: {},
  mounted() {
    // console.log(this.$store.state.ktv);
  },
};
</script>

